"use client";

import { useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { ChevronLeft, Upload, X } from "lucide-react";
import Image from "next/image";

export default function EditLayananPage() {
  const params = useParams();
  const router = useRouter();
  const [gambarLayanan, setGambarLayanan] = useState<string | null>(null);
  const [selectedPeserta, setSelectedPeserta] = useState<string>("Umum");

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setGambarLayanan(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const removeImage = () => {
    setGambarLayanan(null);
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-6 py-4">
        <button
          onClick={() => router.back()}
          className="flex items-center gap-2 text-gray-600 hover:text-gray-900 mb-4"
        >
          <ChevronLeft size={20} />
          <span className="text-sm">Kembali</span>
        </button>
        <h1 className="text-2xl font-semibold text-gray-900">
          Informasi Dasar
        </h1>
      </div>

      {/* Form Content */}
      <div className="max-w-3xl mx-auto p-6">
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
          {/* Nama Layanan */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Nama Layanan
            </label>
            <input
              type="text"
              defaultValue="Pelatihan Kopi"
              className="w-full px-4 py-2.5 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary focus:border-transparent outline-none text-sm"
              placeholder="Nama Layanan"
            />
          </div>

          {/* Gambar Layanan */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Gambar Layanan
            </label>
            <div className="border-2 border-dashed border-gray-300 rounded-md p-6 text-center hover:border-primary transition-colors">
              {gambarLayanan ? (
                <div className="relative">
                  <Image
                    src={gambarLayanan}
                    alt="Preview"
                    width={200}
                    height={200}
                    className="mx-auto rounded-md"
                  />
                  <button
                    onClick={removeImage}
                    className="absolute top-2 right-2 bg-red-500 text-white rounded-full p-1 hover:bg-red-600"
                  >
                    <X size={16} />
                  </button>
                </div>
              ) : (
                <div>
                  <Upload className="mx-auto text-gray-400 mb-2" size={32} />
                  <p className="text-sm text-gray-500 mb-2">Pelatihan.jpg</p>
                  <label className="cursor-pointer">
                    <input
                      type="file"
                      accept="image/*"
                      onChange={handleImageUpload}
                      className="hidden"
                    />
                    <span className="text-sm text-primary hover:underline">
                      Pilih file atau drag and drop
                    </span>
                  </label>
                </div>
              )}
            </div>
          </div>

          {/* Deskripsi Lengkap */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Deskripsi Lengkap
            </label>
            <textarea
              rows={8}
              defaultValue="Program pelatihan kopi ini dirancang untuk memberi peserta pemahaman mendalam tentang industri kopi, mulai dari pengenalan kopi, proses budidaya, pengolahan, hingga menyajikan kopi berkualitas. Peserta akan mempelajari pendidikan teori dan praktek, mulai dari pengenalan jenis tanaman kopi, standar mutu, cara peluang pengemasan serta teknik roasting, brewing, cupping, dan barista. Pelatihan ini terbuka untuk umum dan cocok untuk pelaku UMKM, mahasiswa dan umum yang tertarik mengembangkan potensi bisnis di bisnis kopi."
              className="w-full px-4 py-2.5 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary focus:border-transparent outline-none text-sm resize-none"
              placeholder="Deskripsi"
            />
          </div>

          {/* Deskripsi Singkat */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Deskripsi Singkat (Manual or Card)
            </label>
            <textarea
              rows={2}
              defaultValue="Pelatihan terpadu untuk memahami budidaya, pengolahan dan peluang usaha kopi dari bulu serta."
              className="w-full px-4 py-2.5 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary focus:border-transparent outline-none text-sm resize-none"
              placeholder="Deskripsi singkat"
            />
          </div>

          {/* Durasi */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Durasi
            </label>
            <input
              type="text"
              defaultValue="2-4 Hari"
              className="w-full px-4 py-2.5 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary focus:border-transparent outline-none text-sm"
              placeholder="Durasi"
            />
          </div>

          {/* Peserta */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Peserta
            </label>
            <div className="relative">
              <select
                value={selectedPeserta}
                onChange={(e) => setSelectedPeserta(e.target.value)}
                className="w-full px-4 py-2.5 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary focus:border-transparent outline-none text-sm appearance-none bg-white cursor-pointer"
              >
                <option value="Umum">Umum</option>
                <option value="Siswa / Mahasiswa">Siswa / Mahasiswa</option>
                <option value="Profesional">Profesional</option>
                <option value="UMKM">UMKM</option>
              </select>
              <div className="absolute right-3 top-1/2 -translate-y-1/2 pointer-events-none text-gray-400">
                <svg
                  width="12"
                  height="8"
                  viewBox="0 0 12 8"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M1 1.5L6 6.5L11 1.5"
                    stroke="currentColor"
                    strokeWidth="1.5"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
              </div>
            </div>
            <p className="text-xs text-gray-500 mt-1">
              {selectedPeserta === "Umum" ? "Umum" : selectedPeserta}
              <br />
              Siswa / Mahasiswa
            </p>
          </div>

          {/* Action Buttons */}
          <div className="flex gap-3 pt-4">
            <button
              onClick={() => router.back()}
              className="flex-1 px-6 py-2.5 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors text-sm font-medium"
            >
              Batal
            </button>
            <button className="flex-1 px-6 py-2.5 bg-primary text-white rounded-md hover:bg-primary/90 transition-colors text-sm font-medium">
              Simpan Perubahan
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
